@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@300;400;500;600&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  height: 3rem;
  display: flex;
  flex-direction: row !important;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: white;
  font-family: 'JetBrains Mono', 'monospace';
  font-weight: 600;
}

.compass-button {
  margin: auto;
  margin-top: 20px;
  margin-bottom: 10px;
}

main {
  padding: 0.2rem;
}

@media only screen and (min-device-width: 450px) {
  main {
    padding: 2em;
  }
}

.bar-logo {
  height: 1.5rem;
  margin-right: 5px;
  /* display: inline; */
}

footer {
  text-align: center;
}

footer a {
  color: grey;
}
.compass-arrow {
    height: 3em;
}
.location-block {
    width: -webkit-max-content;
    width: max-content;
    padding: 10px;
    margin: auto;
    margin-top: 20px;
}

.transport-icon {
    width: 15px;
    height: 15px;
    opacity: 0.7;
    margin-right: 5px;
}

.stations-container {
    display: flex !important;
    flex-direction: row !important;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
}

.station {
    margin-top: 20px;
}

@media only screen and (min-device-width: 450px) {
    .station {
        margin-right: 20px;
    }
}

.station-info {
    font-size: 0.8em;
}

.station-box {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    align-content: center;
}

.direction-arrow-box {
    padding: 30px;
}

a {
    text-decoration: none;
    -webkit-filter: brightness(100%);
}

a:hover {
    -webkit-filter: brightness(85%);
    transition: all 0.3s ease-in-out;
}
body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

